
*{margin:0;padding:0;font-family:'Open Sans'
}
html,body{height:100%
}
#app{height:100%
}
header{height:50px;width:100%
}
#user{background:pink;display:inline-block;float:right;line-height:100%;height:100%
}
#user a{line-height:50px
}
nav{height:50px;position:absolute;left:50%;transform:translate(-50%, 0)
}
nav a{color:#232224;font-size:1.3em;display:inline-block;padding:0 10px;height:50px;line-height:50px;text-decoration:none;float:left
}
nav a:hover{background:rgba(0,0,0,0.1);cursor:pointer
}
nav .link-divider{background:#4a4a4a;width:1px;margin-top:12.5px;height:25px;display:inline-block;float:left
}



